*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
} 


nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background-color:rgb(59, 59, 112);
  color: white;
  width:100%;

}
.nav-links{
  width:60%;
  display: flex;
  justify-content: space-around;
  align-items: center;
list-style: none;
font-size: 20px;

}